
.demo-overlay-box{box-shadow:1px 1px 6px rgba(0,0,0,.2);padding:10px 20px;background:#fff
}
.demo-overlay .row{background:#f8f8f8;padding:10px 15px 10px 5px;margin-bottom:120px;*zoom:1
}
.demo-overlay .row:after,.demo-overlay .row:before{display:table;content:""
}
.demo-overlay .row:after{clear:both
}
.demo-overlay pre{float:left;width:400px;margin:0
}
.demo-overlay .preview{margin-left:400px;height:160px;overflow:scroll;border:1px solid #dbdbdb
}
.demo-overlay .preview .scroll-content{width:2000px;height:2000px
}
.demo-overlay .preview .veui-overlay{margin-top:65px;margin-left:100px
}
.demo-overlay .target{background:#6c9;width:100px;height:80px
}
.demo-overlay .veui-button{margin-right:20px
}


