
.veui-schedule{width:653px;border:1px solid #dbdbdb
}
.veui-schedule-header{overflow:hidden;padding:10px 20px;font-size:12px;line-height:24px;border-bottom:1px solid #dbdbdb
}
.veui-schedule-shortcuts{float:left
}
.veui-schedule-shortcut{height:24px;margin-left:10px;padding:0 10px;border:none;background-color:transparent;color:#666;outline:none;transition-property:border-color,background-color,color,opacity;transition-duration:.2s
}
.veui-schedule-shortcut:first-child{margin-left:-10px
}
.veui-schedule-shortcut-selected{background-color:#eee;color:#333
}
.veui-schedule-legend{float:right
}
.veui-schedule-legend-item{margin-left:20px;color:#999
}
.veui-schedule-legend-item:first-child{margin-left:0
}
.veui-schedule-legend-item:before{content:"";display:inline-block;margin-right:8px;width:8px;height:8px
}
.veui-schedule-legend-available:before{border:1px solid #dbdbdb;background-color:#fff
}
.veui-schedule-legend-selected:before{background-color:#c4e0fe
}
.veui-schedule-body{*zoom:1;padding:20px 10px 20px 20px;background-color:#f7f7f7
}
.veui-schedule-body:after,.veui-schedule-body:before{display:table;content:""
}
.veui-schedule-body:after{clear:both
}
.veui-schedule-head-hour{float:right;color:#999;margin-bottom:10px;font-size:12px
}
.veui-schedule-head-hour-item{float:left;width:46px;height:12px;text-align:center
}
.veui-schedule-head-day{float:left;clear:right;margin-right:10px;color:#999
}
.veui-schedule-head-day-item{line-height:23px
}
.veui-schedule-head-day-item .veui-checkbox[ui~=small] .veui-checkbox-box{margin-right:10px
}
.veui-schedule-head-day-item .veui-checkbox[ui~=small] .veui-checkbox-label{font-size:14px
}
.veui-schedule-detail{position:relative;float:left
}
.veui-schedule-table{table-layout:fixed;border-collapse:collapse;cursor:pointer
}
.veui-schedule-table col{width:23px
}
.veui-schedule-table td{height:23px;padding:0;text-align:center
}
.veui-schedule-table-interaction button{position:absolute;top:0;left:0;width:22px;height:22px;border:none;background-color:currentColor;color:#fff;outline:none;transition-property:border-color,background-color,color,opacity;transition-duration:.2s
}
.veui-schedule-table-interaction button.focus-ring,.veui-schedule-table-interaction button:hover{color:#ebf5ff
}
.veui-schedule-table-interaction button.veui-schedule-selected,.veui-schedule-table-interaction button:hover:active{color:#c4e0fe
}
.veui-schedule-table-interaction button.veui-schedule-selected.focus-ring,.veui-schedule-table-interaction button.veui-schedule-selected:hover{color:#b0d6fe
}
.veui-schedule-table-interaction button.veui-schedule-selected:hover:active{color:#88c1fd
}
.veui-schedule-table-interaction button:after{content:"";position:absolute;top:0;left:0;width:23px;height:23px;z-index:1;background-color:#fff;opacity:.001
}
.veui-schedule-table-interaction td{position:relative;border:1px solid #e7e7e7
}
.veui-schedule-table-interaction td.veui-schedule-selected+.veui-schedule-selected button:before{content:"";position:absolute;top:0;left:-1px;width:1px;height:22px;background-color:currentColor
}
.veui-schedule-table-selected{pointer-events:none;position:absolute;top:0;left:0;font-size:12px;color:#666
}
.veui-schedule-table-selected td{padding-top:1px;border-color:transparent;white-space:nowrap
}


