
.veui-tooltip{position:relative;padding:7.5px 10px;background-color:rgba(51,51,51,.8);color:#fff;line-height:1.5;border-radius:1px;box-shadow:0 1px 4px rgba(0,0,0,.2)
}
.veui-tooltip-box-transparent{pointer-events:none
}
.veui-tooltip:after,.veui-tooltip:before{content:"";color:rgba(51,51,51,.8);-ms-transform-origin:50% 100%;transform-origin:50% 100%;top:50%;left:50%;position:absolute;width:0;height:0;border:4px solid transparent;border-bottom-color:currentColor;margin-left:-4px;margin-top:-8px
}
.veui-tooltip:before{display:none
}
.tether-element-attached-top.tether-target-attached-bottom .veui-tooltip{margin-top:8px
}
.tether-element-attached-top.tether-target-attached-bottom .veui-tooltip:after,.tether-element-attached-top.tether-target-attached-bottom .veui-tooltip:before{top:0
}
.tether-element-attached-top.tether-target-attached-bottom .veui-tooltip:before{-ms-transform:scale(1.3);transform:scale(1.3)
}
.tether-element-attached-right.tether-target-attached-left .veui-tooltip{margin-right:8px
}
.tether-element-attached-right.tether-target-attached-left .veui-tooltip:after,.tether-element-attached-right.tether-target-attached-left .veui-tooltip:before{right:-4px;left:auto;-ms-transform:scale(1.1) rotate(90deg);transform:scale(1.1) rotate(90deg)
}
.tether-element-attached-right.tether-target-attached-left .veui-tooltip:before{-ms-transform:scale(1.3) rotate(90deg);transform:scale(1.3) rotate(90deg)
}
.tether-element-attached-bottom.tether-target-attached-top .veui-tooltip{margin-bottom:8px
}
.tether-element-attached-bottom.tether-target-attached-top .veui-tooltip:after,.tether-element-attached-bottom.tether-target-attached-top .veui-tooltip:before{bottom:0;top:auto;-ms-transform:scale(1.1) rotate(180deg);transform:scale(1.1) rotate(180deg)
}
.tether-element-attached-bottom.tether-target-attached-top .veui-tooltip:before{-ms-transform:scale(1.3) rotate(180deg);transform:scale(1.3) rotate(180deg)
}
.tether-element-attached-left.tether-target-attached-right .veui-tooltip{margin-left:8px
}
.tether-element-attached-left.tether-target-attached-right .veui-tooltip:after,.tether-element-attached-left.tether-target-attached-right .veui-tooltip:before{left:0;-ms-transform:scale(1.1) rotate(270deg);transform:scale(1.1) rotate(270deg)
}
.tether-element-attached-left.tether-target-attached-right .veui-tooltip:before{-ms-transform:scale(1.3) rotate(270deg);transform:scale(1.3) rotate(270deg)
}
.tether-element-attached-top.tether-target-attached-top .veui-tooltip:after,.tether-element-attached-top.tether-target-attached-top .veui-tooltip:before{top:14px
}
.tether-element-attached-right.tether-target-attached-right .veui-tooltip:after,.tether-element-attached-right.tether-target-attached-right .veui-tooltip:before{right:14px;left:auto
}
.tether-element-attached-bottom.tether-target-attached-bottom .veui-tooltip:after,.tether-element-attached-bottom.tether-target-attached-bottom .veui-tooltip:before{top:auto;bottom:14px
}
.tether-element-attached-left.tether-target-attached-left .veui-tooltip:after,.tether-element-attached-left.tether-target-attached-left .veui-tooltip:before{left:14px
}
.veui-tooltip[ui~=alt]{background-color:#fff;color:#666
}
.veui-tooltip[ui~=alt]:before{display:block;color:rgba(0,0,0,.1);z-index:-1
}
.veui-tooltip[ui~=alt]:after{color:#fff
}
.veui-tooltip[ui~=small]{font-size:12px
}
.veui-tooltip[ui~=large]{font-size:16px
}


