
.veui-radio{display:inline-block;color:#666;font-size:14px;line-height:16px;cursor:pointer
}
.veui-radio .veui-icon,.veui-radio input{display:none
}
.veui-radio-box,.veui-radio-label{vertical-align:top;display:inline-block
}
.veui-field>.veui-radio{line-height:36px
}
.veui-field>.veui-radio .veui-radio-box{vertical-align:text-top
}
.veui-radio:hover{color:#333
}
.veui-radio-box{position:relative;height:16px;width:16px;border:1px solid #666;background-color:#fff;margin-right:8px;border-radius:50%;font-size:14px
}
.veui-radio :checked+.veui-radio-box,.veui-radio:hover .veui-radio-box{border-color:#333
}
.veui-radio :checked+.veui-radio-box:before{content:"";display:block;width:16px;height:16px;background-color:#333;border-radius:50%;position:absolute;top:50%;right:50%;bottom:50%;left:50%;-ms-transform:translate(-50%,-50%) scale(.5);transform:translate(-50%,-50%) scale(.5)
}
.veui-radio.veui-disabled{cursor:not-allowed
}
.veui-radio.veui-disabled .veui-radio-label{color:#999
}
.veui-radio :disabled+.veui-radio-box{border-color:#dbdbdb;background-color:#eee
}
.veui-radio :disabled+.veui-radio-box:before{background-color:#dbdbdb
}
.veui-radio[ui~=small]{font-size:12px;line-height:12px
}
.veui-field[ui~=small]>.veui-radio[ui~=small]{line-height:32px
}
.veui-field[ui~=small]>.veui-radio[ui~=small] .veui-radio-box{vertical-align:-2px
}
.veui-radio[ui~=small] .veui-radio-box{margin-right:6px;font-size:12px;height:12px;width:12px
}
.veui-radio[ui~=small] .veui-radio-box:before{width:12px;height:12px
}


