
.veui-field{vertical-align:top;clear:both
}
.veui-field-required>.veui-form-label:after{content:"*";float:right;margin-right:9px;color:#ff5b5b
}
.veui-field-no-label:before{content:"";display:inline-block;width:120px
}
.veui-field>*{display:inline-block;vertical-align:top
}
.veui-field>:not([class*=veui-form-label])+*{margin-left:10px
}
.veui-field .veui-form-label{height:36px;width:120px;line-height:36px
}
.veui-field .veui-span{line-height:36px
}
.veui-field-error{position:static;height:36px;line-height:36px;margin:0 0 0 10px;vertical-align:top;color:#ff5b5b
}
.veui-field-error .veui-icon{margin-right:7px
}
.veui-field .veui-form-tip{height:36px;line-height:36px;margin-left:10px;color:#999
}
.veui-field .veui-form-tip+.veui-field-error{position:absolute;display:block;margin:0;height:30px;line-height:30px;font-size:12px
}
.veui-field .veui-form-tip+.veui-field-error .veui-icon{margin-top:calc(30px / 2 - .5em)
}
.veui-field .veui-form-label~.veui-form-tip+.veui-field-error{margin-left:120px
}
.veui-field .veui-field-error .veui-icon,.veui-field .veui-form-label .veui-icon,.veui-field .veui-form-tip .veui-icon,.veui-field .veui-span .veui-icon{vertical-align:top;margin-top:calc(36px / 2 - .5em)
}
.veui-field[ui~=small]{font-size:12px
}
.veui-field[ui~=small] .veui-field-error,.veui-field[ui~=small] .veui-form-label,.veui-field[ui~=small] .veui-form-tip,.veui-field[ui~=small] .veui-span{line-height:32px
}
.veui-field[ui~=small] .veui-field-error .veui-icon,.veui-field[ui~=small] .veui-form-label .veui-icon,.veui-field[ui~=small] .veui-form-tip .veui-icon,.veui-field[ui~=small] .veui-span .veui-icon{margin-top:calc(32px / 2 - .5em)
}
.veui-field[ui~=small] .veui-field-error,.veui-field[ui~=small] .veui-form-label,.veui-field[ui~=small] .veui-form-tip{height:32px
}
.veui-field[ui~=large]{font-size:16px
}
.veui-field[ui~=large] .veui-field-error,.veui-field[ui~=large] .veui-form-label,.veui-field[ui~=large] .veui-form-tip,.veui-field[ui~=large] .veui-span{line-height:42px
}
.veui-field[ui~=large] .veui-field-error .veui-icon,.veui-field[ui~=large] .veui-form-label .veui-icon,.veui-field[ui~=large] .veui-form-tip .veui-icon,.veui-field[ui~=large] .veui-span .veui-icon{margin-top:calc(42px / 2 - .5em)
}
.veui-field[ui~=large] .veui-field-error,.veui-field[ui~=large] .veui-form-label,.veui-field[ui~=large] .veui-form-tip{height:42px
}


