
.veui-switch{display:inline-block;cursor:pointer
}
.veui-switch .veui-switch-switcher{width:50px;height:24px;border-radius:12px
}
.veui-switch .veui-switch-button{width:20px;height:20px
}
.veui-switch .veui-switch-button .veui-icon{width:16px;height:16px
}
.veui-switch .veui-switch-label{line-height:24px
}
.veui-switch.veui-switch-on .veui-switch-button{-ms-transform:translateX(26px);transform:translateX(26px)
}
.veui-switch-label,.veui-switch-switcher{vertical-align:middle
}
.veui-switch-label{display:inline-block;margin-left:10px
}
.veui-switch-switcher{display:inline-block;background-color:#b8b8b8;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;transition:background-color .2s ease
}
.veui-switch-button{display:inline-block;position:relative;margin:2px;background-color:#fff;border-radius:50%;transition:transform .2s ease
}
.veui-switch-button .veui-icon{position:absolute;top:50%;left:50%;-ms-transform:translate(-50%,-50%);transform:translate(-50%,-50%)
}
.veui-switch-on .veui-switch-switcher{background-color:#3998fc
}
.veui-switch-readonly{cursor:default
}
.veui-switch-disabled{cursor:not-allowed
}
.veui-switch-disabled .veui-switch-switcher{background-color:#dbdbdb
}
.veui-switch-on.veui-switch-disabled .veui-switch-switcher{background-color:#c4e0fe
}
.veui-switch-disabled .veui-switch-button{background-color:#b8b8b8
}
.veui-switch[ui~=small] .veui-switch-switcher{width:37.5px;height:18px;border-radius:9px
}
.veui-switch[ui~=small] .veui-switch-button{width:14px;height:14px
}
.veui-switch[ui~=small] .veui-switch-button .veui-icon{width:12px;height:12px
}
.veui-switch[ui~=small] .veui-switch-label{line-height:18px
}
.veui-switch[ui~=small].veui-switch-on .veui-switch-button{-ms-transform:translateX(19.5px);transform:translateX(19.5px)
}
.veui-switch[ui~=large] .veui-switch-switcher{width:62.5px;height:30px;border-radius:15px
}
.veui-switch[ui~=large] .veui-switch-button{width:26px;height:26px
}
.veui-switch[ui~=large] .veui-switch-button .veui-icon{width:20px;height:20px
}
.veui-switch[ui~=large] .veui-switch-label{line-height:30px
}
.veui-switch[ui~=large].veui-switch-on .veui-switch-button{-ms-transform:translateX(32.5px);transform:translateX(32.5px)
}
.veui-switch input{display:none
}


