
.veui-alert{margin-top:30px;margin-bottom:30px;position:relative;padding:10px 20px;color:#666
}
.veui-alert span{display:inline-block
}
.veui-alert-icon{-ms-transform:translateY(-50%);transform:translateY(-50%);position:absolute;top:50%;left:20px;font-size:16px
}
.veui-alert-message{margin:0 56px 0 36px
}
.veui-alert-success{background-color:#eff9f5
}
.veui-alert-success .veui-alert-icon{color:#5bc49f
}
.veui-alert-warning{background-color:#fff5e6
}
.veui-alert-warning .veui-alert-icon{color:#fe9700
}
.veui-alert-info{background-color:#ebf5ff
}
.veui-alert-info .veui-alert-icon{color:#3998fc
}
.veui-alert-error{background-color:#ffefef
}
.veui-alert-error .veui-alert-icon{color:#ff5b5b
}
.veui-alert-close,.veui-alert-nav{position:absolute;top:50%;right:20px;-ms-transform:translateY(-50%);transform:translateY(-50%)
}
.veui-alert-nav .veui-button:first-child{margin-right:10px
}
.veui-alert-close.veui-button .veui-icon,.veui-alert-nav .veui-button .veui-icon{font-size:12px
}
.veui-alert-close.veui-button.focus-ring,.veui-alert-close.veui-button:active,.veui-alert-close.veui-button:hover,.veui-alert-nav .veui-button.focus-ring,.veui-alert-nav .veui-button:active,.veui-alert-nav .veui-button:hover{color:#333
}
.veui-alert .veui-alert-close-text{color:#3998fc
}
.veui-alert .veui-alert-close-text.focus-ring,.veui-alert .veui-alert-close-text:hover{color:#3389e3
}
.veui-alert .veui-alert-close-text:active{color:#2e7aca
}


