
.veui-tabs{position:relative;padding-bottom:1px
}
.veui-tabs-menu{border-bottom:1px solid #e7e7e7;overflow:hidden;white-space:nowrap
}
.veui-tabs-list{position:relative;display:inline-block;padding:0;margin:0
}
.veui-tabs-list object{position:absolute;top:0;left:0;width:100%;height:100%;overflow:hidden;pointer-events:none;z-index:-1
}
.veui-tabs-item{display:inline-block;margin-left:60px
}
.veui-tabs-item:first-child{margin-left:0
}
.veui-tabs-item>.veui-icon{visibility:hidden;color:#999;cursor:pointer
}
.veui-tabs-item>.veui-icon:hover{color:#666
}
.veui-tabs-item>.veui-icon:active{color:#333
}
.veui-tabs-item:hover>.veui-icon{visibility:visible
}
.veui-tabs-item>a,.veui-tabs-item>span{cursor:pointer;padding:0 10px;line-height:2.5;display:inline-block;text-decoration:none;outline:none;position:relative
}
.veui-tabs-item:not(.veui-tabs-item-disabled)>a:after,.veui-tabs-item:not(.veui-tabs-item-disabled)>span:after{content:"";position:absolute;bottom:0;left:10px;width:calc(100% - 20px);height:2px
}
.veui-tabs-item:not(.veui-tabs-item-disabled)>a:hover:after,.veui-tabs-item:not(.veui-tabs-item-disabled)>span:hover:after{background-color:#999
}
.veui-tabs-item-active>a:after,.veui-tabs-item-active>span:after,.veui-tabs-item:not(.veui-tabs-item-disabled)>a:active:after,.veui-tabs-item:not(.veui-tabs-item-disabled)>span:active:after{background-color:#333
}
.veui-tabs-item:first-child>a,.veui-tabs-item:first-child>span{margin-left:-10px
}
.veui-tabs-item-disabled>a,.veui-tabs-item-disabled>span{cursor:not-allowed;color:#999
}
.veui-tabs-extra{display:inline-block;padding-left:60px;background-color:#fff
}
.veui-tabs-extra.veui-tabs-extra-overflow{position:absolute;right:0
}
.veui-tabs-operator{display:inline-block;line-height:2.5;cursor:pointer
}
.veui-tabs-operator .veui-icon{margin-right:6px
}
.veui-tabs-item .veui-icon,.veui-tabs-operator .veui-icon{vertical-align:top;margin-top:calc(36px / 2 - .5em);-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
.veui-tabs-scroller{display:inline-block;line-height:2.5;margin-left:30px;vertical-align:top
}
.veui-tabs-scroller-left,.veui-tabs-scroller-right{display:inline-block;margin-left:5px;width:36px;background-color:#f7f7f7;text-align:center;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
.veui-tabs-scroller-left:first-child,.veui-tabs-scroller-right:first-child{margin-left:0
}
.veui-tabs-scroller-left .veui-icon,.veui-tabs-scroller-right .veui-icon{vertical-align:middle
}
.veui-tabs-scroller-left:hover,.veui-tabs-scroller-right:hover{background-color:#dbdbdb
}
.veui-tabs-scroller-left:active,.veui-tabs-scroller-right:active{background-color:#999
}
.veui-tabs[ui~=large] .veui-tabs-item{font-size:16px;margin-left:60px
}
.veui-tabs[ui~=large] .veui-tabs-item:first-child{margin-left:0
}
.veui-tabs[ui~=large] .veui-tabs-operator{padding:0 10px;font-size:16px
}
.veui-tabs[ui~=large] .veui-tabs-item .veui-icon,.veui-tabs[ui~=large] .veui-tabs-operator .veui-icon{margin-top:calc(42px / 2 - .5em)
}
.veui-tabs[ui~=small] .veui-tabs-item{margin-left:40px
}
.veui-tabs[ui~=small] .veui-tabs-item:first-child{margin-left:0
}
.veui-tabs[ui~=small] .veui-tabs-menu,.veui-tabs[ui~=tiny] .veui-tabs-menu{border-bottom:none
}
.veui-tabs[ui~=tiny] .veui-tabs-item{margin-left:20px;position:relative
}
.veui-tabs[ui~=tiny] .veui-tabs-item:first-child{margin-left:0
}
.veui-tabs[ui~=tiny] .veui-tabs-item:before{content:"";display:inline-block;width:1px;height:14px;background-color:#e7e7e7;position:absolute;top:10px;left:-10px
}
.veui-tabs[ui~=tiny] .veui-tabs-item:first-child:before{display:none
}


