
.veui-fieldset{position:relative;clear:both
}
.veui-fieldset-required>.veui-form-label:after{content:"*";float:right;margin-right:9px;color:#ff5b5b
}
.veui-fieldset.veui-field-no-label:before,.veui-fieldset>.veui-field-no-label:before{width:auto
}
.veui-fieldset>:not([class*=veui-form-label])+*{margin-left:10px
}
.veui-fieldset[ui~=alt]>.veui-field:first-child .veui-form-label,.veui-fieldset[ui~=alt]>.veui-form-label{padding-left:10px
}
.veui-fieldset[ui~=alt] [class*=veui][ui~=alt]{border-color:transparent;box-shadow:none
}
.veui-fieldset[ui~=alt],.veui-fieldset[ui~=alt]>.veui-field>.veui-form-label,.veui-fieldset[ui~=alt]>.veui-form-label{background-color:#eee;border-color:#eee;color:#666
}
.veui-fieldset>.veui-field .veui-field-error,.veui-fieldset>.veui-field:not(.veui-field-no-tip)+.veui-field-error{position:absolute;display:block;margin:0;height:30px;line-height:30px;font-size:12px
}
.veui-fieldset>.veui-field .veui-field-error .veui-icon,.veui-fieldset>.veui-field:not(.veui-field-no-tip)+.veui-field-error .veui-icon{margin-top:calc(30px / 2 - .5em)
}
.veui-fieldset>.veui-field:not(.veui-field-no-label)~.veui-field-error,.veui-fieldset>.veui-field:not(.veui-field-no-tip)+.veui-field-error{margin-left:120px
}
.veui-fieldset.veui-field-no-tip>.veui-field-no-tip:last-child>.veui-field-error{position:static;display:inline-block;margin-left:10px;height:36px;line-height:36px;font-size:inherit
}
.veui-fieldset.veui-field-no-tip>.veui-field-no-tip:last-child>.veui-field-error .veui-icon{margin-top:calc(36px / 2 - .5em)
}


