
.veui-toast-list{position:fixed;top:85px;right:0;left:0
}
.veui-toast-list .veui-toast{left:50%;-ms-transform:translateX(-50%);transform:translateX(-50%);transition:opacity .3s,top .3s,transform .3s
}
.veui-toast-list .veui-toast-enter,.veui-toast-list .veui-toast-leave-to{opacity:0
}
.veui-toast-list .veui-toast-enter{-ms-transform:translate(-50%,-100%);transform:translate(-50%,-100%)
}


