
.veui-table{table-layout:fixed;width:100%;border-collapse:collapse;color:#666
}
.veui-table-cell{display:inline-block;line-height:2;vertical-align:middle
}
.veui-table td,.veui-table th{height:54px;padding-right:16px;padding-left:16px;text-align:left;white-space:nowrap
}
.veui-table th{font-weight:500
}
.veui-table td{border:1px solid #eee;border-style:solid none
}
.veui-table tfoot th{border-top:none
}
td.veui-table-no-data{height:300px;text-align:center
}
.veui-table .veui-table-selected-row td{background-color:#fcfcfc
}
.veui-table tr:hover td{background-color:#f7f7f7
}
.veui-table tr:hover .veui-table-no-data{background-color:transparent
}
.veui-table .veui-button+.veui-button{margin-left:30px
}
.veui-table-header{display:inline-block;vertical-align:middle
}
.veui-table .veui-table-column-center{text-align:center
}
.veui-table .veui-table-column-right{text-align:right;padding-right:38px
}
.veui-table .veui-sorter{margin-left:5px;font-size:12px
}
.veui-table[ui~=slim] td,.veui-table[ui~=slim] th{height:44px
}
.veui-table[ui~=alt] thead th{background-color:#f7f7f7
}


