
.veui-filter-panel{display:inline-block;overflow:hidden;min-width:230px;height:358px;border:1px solid #e7e7e7
}
.veui-filter-panel-title{height:36px;line-height:36px;margin:10px 20px;color:#333;font-size:14px
}
.veui-filter-panel .veui-searchbox{margin:0 20px 8px;width:calc(100% - 40px)
}
.veui-filter-panel-no-data{color:#b8b8b8;margin:12px 20px 0
}
.veui-filter-panel-content{height:calc(100% - 56px)
}
.veui-filter-panel-content-main{height:calc(100% - 40px);overflow-y:auto
}


