
.veui-checkbox{display:inline-block;color:#666;font-size:14px;line-height:16px;cursor:pointer
}
.veui-checkbox .veui-icon,.veui-checkbox input{display:none
}
.veui-checkbox-box,.veui-checkbox-label{vertical-align:top;display:inline-block
}
.veui-field>.veui-checkbox{line-height:36px
}
.veui-field>.veui-checkbox .veui-checkbox-box{vertical-align:text-top
}
.veui-checkbox:hover{color:#333
}
.veui-checkbox-box{position:relative;height:16px;width:16px;margin-right:8px;border:1px solid #666;background-color:#fff;font-size:16px
}
.veui-checkbox:hover .veui-checkbox-box{border-color:#333
}
.veui-checkbox :checked+.veui-checkbox-box,.veui-checkbox :indeterminate+.veui-checkbox-box{border-color:#333
}
.veui-checkbox :checked+.veui-checkbox-box>.veui-icon,.veui-checkbox :indeterminate+.veui-checkbox-box>.veui-icon{display:block;color:#333;position:absolute;top:50%;right:50%;bottom:50%;left:50%;-ms-transform:translate(-50%,-50%);transform:translate(-50%,-50%)
}
.veui-checkbox.veui-disabled{cursor:not-allowed
}
.veui-checkbox.veui-disabled .veui-checkbox-label{color:#999
}
.veui-checkbox.veui-disabled .veui-checkbox-box{border-color:#dbdbdb;background-color:#eee
}
.veui-checkbox.veui-disabled .veui-checkbox-box .veui-icon{color:#dbdbdb
}
.veui-checkbox[ui~=small]{font-size:12px;line-height:12px
}
.veui-field[ui~=small]>.veui-checkbox[ui~=small]{line-height:32px
}
.veui-field[ui~=small]>.veui-checkbox[ui~=small] .veui-checkbox-box{vertical-align:-2px
}
.veui-checkbox[ui~=small] .veui-checkbox-box{margin-right:6px;height:12px;width:12px;font-size:12px
}


