
.veui-carousel{position:relative;width:320px;height:180px
}
.veui-carousel-viewport{width:320px;height:180px;margin:0 auto
}
.veui-carousel-items{position:relative;list-style:none;width:100%;height:100%;margin:0;padding:0
}
.veui-carousel-item{position:absolute;top:0;right:0;bottom:0;left:0;overflow:hidden;background:no-repeat 50%/cover;transition:opacity .3s
}
.veui-carousel-item img{visibility:hidden
}
.veui-carousel-item-enter,.veui-carousel-item-leave-to{opacity:0
}
.veui-carousel-control,.veui-carousel-indicator-radios .veui-carousel-indicator-item{border:none;padding:0;background-color:rgba(0,0,0,.2);color:#fff;transition-property:border-color,background-color,color,opacity;transition-duration:.2s
}
.veui-carousel-control:focus,.veui-carousel-indicator-radios .veui-carousel-indicator-item:focus{outline:none
}
.veui-carousel-control.focus-ring,.veui-carousel-control:hover,.veui-carousel-indicator-radios .veui-carousel-indicator-item.focus-ring,.veui-carousel-indicator-radios .veui-carousel-indicator-item:hover{background-color:rgba(0,0,0,.4)
}
.veui-carousel-control{position:absolute;top:74px;width:32px;height:32px;line-height:32px;vertical-align:middle
}
.veui-carousel-control:disabled{opacity:.5;cursor:default
}
.veui-carousel-control:disabled:hover{background-color:rgba(0,0,0,.2)
}
.veui-carousel-control-prev{left:10px
}
.veui-carousel-control-next{right:10px
}
.veui-carousel-control .veui-icon{position:absolute;top:50%;left:50%;-ms-transform:translate(-50%,-50%);transform:translate(-50%,-50%)
}
.veui-carousel-indicator-radios{display:inline-block;position:absolute;bottom:10px;left:50%;-ms-transform:translateX(-50%);transform:translateX(-50%)
}
.veui-carousel-indicator-radios .veui-carousel-indicator-item{float:left;margin-left:2px;width:18px;height:3px;text-indent:-9999px;overflow:hidden;text-align:left
}
.veui-carousel-indicator-radios .veui-carousel-indicator-item:first-child{margin-left:0
}
.veui-carousel-indicator-radios .veui-carousel-indicator-item-current,.veui-carousel-indicator-radios .veui-carousel-indicator-item-current.focus-ring,.veui-carousel-indicator-radios .veui-carousel-indicator-item-current:hover{background-color:#fff;box-shadow:0 0 4px rgba(0,0,0,.2)
}
.veui-carousel-indicator-numbers{position:absolute;bottom:8px;left:50%;-ms-transform:translateX(-50%);transform:translateX(-50%);background-color:rgba(0,0,0,.2);color:#fff;padding:0 4px;height:16px;line-height:16px;font-size:12px;border-radius:1px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;cursor:default
}
.veui-carousel-indicator-numbers-separator{margin-right:2px;margin-left:2px
}
.veui-carousel-indicator-numbers-separator:after{content:"";display:inline-block;width:1px;height:8px;background-color:currentColor;-ms-transform:rotate(15deg);transform:rotate(15deg)
}
.veui-carousel-indicator-tabs{display:none;position:absolute;right:0;bottom:0;left:0;background-image:linear-gradient(180deg,transparent,#333);background-repeat:repeat-x;filter:progid:DXImageTransform.Microsoft.Gradient(startColorstr="#00000000",endColorstr="#ff333333",GradientType=0);text-align:right;font-size:14px
}
.veui-carousel-indicator-tabs .veui-carousel-indicator-item{height:54px;line-height:54px;margin-right:20px;padding:0;border:none;background:none;color:hsla(0,0%,100%,.5)
}
.veui-carousel-indicator-tabs .veui-carousel-indicator-item:focus{outline:none
}
.veui-carousel-indicator-tabs .veui-carousel-indicator-item-current,.veui-carousel-indicator-tabs .veui-carousel-indicator-item-current:hover,.veui-carousel-indicator-tabs .veui-carousel-indicator-item.focus-ring,.veui-carousel-indicator-tabs .veui-carousel-indicator-item:hover{color:#fff
}
.veui-carousel[ui~=loose]{width:444px
}
.veui-carousel[ui~=loose] .veui-carousel-control-prev{left:0
}
.veui-carousel[ui~=loose] .veui-carousel-control-next{right:0
}
.veui-carousel[ui~=loose] .veui-carousel-indicator-numbers,.veui-carousel[ui~=loose] .veui-carousel-indicator-radios{position:static;float:left;margin-top:10px;margin-left:50%
}
.veui-carousel[ui~=loose] .veui-carousel-indicator-numbers{background-color:#eee;color:#999
}
.veui-carousel[ui~=loose] .veui-carousel-indicator-item-current,.veui-carousel[ui~=loose] .veui-carousel-indicator-item.focus-ring,.veui-carousel[ui~=loose] .veui-carousel-indicator-item:hover{background-color:#333
}
.veui-carousel[ui~=small]{width:160px;height:90px
}
.veui-carousel[ui~=small][ui~=loose]{width:232px;min-height:90px;height:auto
}
.veui-carousel[ui~=small] .veui-carousel-viewport{width:160px;height:90px
}
.veui-carousel[ui~=small] .veui-carousel-control{position:absolute;top:37px;width:16px;height:16px;line-height:16px
}
.veui-carousel[ui~=small] .veui-carousel-control .veui-icon{position:absolute;top:50%;left:50%;-ms-transform:translate(-50%,-50%) scale(.5);transform:translate(-50%,-50%) scale(.5)
}
.veui-carousel[ui~=small] .veui-carousel-indicator-radios .veui-carousel[ui~=small] .veui-carousel-indicator-item{width:14px;height:2px;box-shadow:none
}
.veui-carousel[ui~=large]{width:640px;height:360px
}
.veui-carousel[ui~=large][ui~=loose]{width:816px;min-height:360px;height:auto
}
.veui-carousel[ui~=large][ui~=loose] .veui-carousel-indicator-numbers,.veui-carousel[ui~=large][ui~=loose] .veui-carousel-indicator-radios{margin-top:20px
}
.veui-carousel[ui~=large][ui~=loose] .veui-carousel-indicator-tabs{display:none
}
.veui-carousel[ui~=large] .veui-carousel-viewport{width:640px;height:360px
}
.veui-carousel[ui~=large] .veui-carousel-control{position:absolute;top:156px;width:48px;height:48px;line-height:48px
}
.veui-carousel[ui~=large] .veui-carousel-control .veui-icon{position:absolute;top:50%;left:50%;-ms-transform:translate(-50%,-50%) scale(1.5);transform:translate(-50%,-50%) scale(1.5)
}
.veui-carousel[ui~=large] .veui-carousel-indicator-tabs{display:block
}
.veui-carousel[ui~=large] .veui-carousel-indicator-numbers{padding:0 6px;font-size:16px;height:20px;line-height:20px
}
.veui-carousel[ui~=large] .veui-carousel-indicator-numbers-separator{margin-right:4px;margin-left:4px
}
.veui-carousel[ui~=large] .veui-carousel-indicator-numbers-separator:after{height:11px
}
.veui-carousel[ui~=large] .veui-carousel-indicator-radios .veui-carousel[ui~=large] .veui-carousel-indicator-item{width:30px;height:4px
}


