
.veui-field-invalid .veui-input,.veui-field-invalid .veui-input:focus,.veui-field-invalid .veui-input:hover,.veui-field-invalid .veui-textarea,.veui-field-invalid .veui-textarea:focus,.veui-field-invalid .veui-textarea:hover{border-color:#ff5b5b
}
.veui-input,.veui-textarea{height:36px;line-height:34px;width:210px;padding:0 10px;vertical-align:middle;border:1px solid #dbdbdb;background-color:#fff;color:#666
