.veui-input:disabled,.veui-input:read-only,.veui-textarea:disabled,.veui-textarea:read-only{background-color:#f7f7f7;border-color:#dbdbdb;color:#999
}
.veui-input:disabled,.veui-textarea:disabled{cursor:not-allowed
}
.veui-input::-webkit-input-placeholder,.veui-textarea::-webkit-input-placeholder{color:#999
}
.veui-input:-ms-input-placeholder,.veui-textarea:-ms-input-placeholder{color:#999
}
.veui-input::placeholder,.veui-textarea::placeholder{color:#999
}
.veui-input[ui~=large],.veui-textarea[ui~=large]{height:42px;line-height:40px;font-size:16px
