
.veui-select-options{border:1px solid #e7e7e7;margin:3px 0;box-shadow:0 1px 4px rgba(0,0,0,.2)
}
.veui-select-button{width:100%;padding-right:10px;padding-left:10px;text-align:left
}
.veui-select-label{display:inline-block;max-width:calc(100% - 24px);width:100%;overflow:hidden;text-overflow:ellipsis;white-space:nowrap
}
.veui-select-icon{float:right
}
.veui-select-button[ui~=primary]:not(.veui-button-loading):disabled{background-color:#eee;border-color:transparent;color:#999
}
.veui-select-expanded .veui-select-button:not([ui~=link]),.veui-select-expanded .veui-select-button:not([ui~=link]).focus-ring,.veui-select-expanded .veui-select-button:not([ui~=link]):hover{color:#333;background-color:#f7f7f7
}
.veui-select-expanded .veui-select-button:not([ui~=link]):active{background-color:#e7e7e7
}
.veui-select-expanded .veui-select-button[ui~=primary]:not([ui~=link]),.veui-select-expanded .veui-select-button[ui~=primary]:not([ui~=link]).focus-ring,.veui-select-expanded .veui-select-button[ui~=primary]:not([ui~=link]):hover{background-color:#3389e3;color:#fff
}
.veui-select-expanded .veui-select-button[ui~=primary]:not([ui~=link]):active{background-color:#2e7aca
}
.veui-select-expanded .veui-select-button[ui~=alt],.veui-select-expanded .veui-select-button[ui~=alt].focus-ring,.veui-select-expanded .veui-select-button[ui~=alt]:hover{background-color:#e7e7e7
}
.veui-field-invalid .veui-select .veui-button.veui-select-button,.veui-field-invalid .veui-select .veui-button.veui-select-button:focus,.veui-field-invalid .veui-select .veui-button.veui-select-button:hover{border-color:#ff5b5b
}
.veui-select{display:inline-block;min-width:110px
}
.veui-select-options{min-width:110px;max-height:280px;overflow-y:auto;background-color:#fff
}
.veui-select-option-group-label{display:inline-block;height:36px;line-height:36px;padding:0 10px;font-size:12px;color:#999
}
.veui-select-option-group .veui-option{padding-left:20px
}
.veui-select-option-group-unlabelled{border-top:1px solid #e7e7e7
}
.veui-select-option-group-unlabelled:first-child{border-top:none
}
.veui-select-option-group-unlabelled .veui-option{padding-left:10px
}


