
.veui-searchbox-suggestion-overlay{border:1px solid #e7e7e7;margin:3px 0;box-shadow:0 1px 4px rgba(0,0,0,.2)
}
.veui-searchbox-suggestion-item{transition-property:border-color,background-color,color,opacity;transition-duration:.2s;height:36px;line-height:36px;overflow:hidden;text-overflow:ellipsis;white-space:nowrap;width:100%;padding:0 10px;color:#666;cursor:pointer
}
.veui-searchbox-suggestion-item:hover{background-color:#f7f7f7
}
.veui-searchbox-suggestion-item:active{background-color:#e7e7e7
}
.veui-searchbox-suggestion-item-selected{color:#333
}
.veui-searchbox-suggestion-item-disabled{background-color:#fff;color:#999;cursor:not-allowed
}
.veui-searchbox-suggestion-item-label{margin-right:10px
}
.veui-searchbox{display:inline-block;position:relative;color:#999;width:210px
}
.veui-searchbox .veui-input{width:100%
}
.veui-searchbox.veui-searchbox-clearable .veui-searchbox-icon-close{display:inline-block
}
.veui-searchbox-icon-search .veui-button{display:none
}
.veui-searchbox.veui-disabled,.veui-searchbox.veui-readonly{background-color:#f7f7f7;border-color:#dbdbdb;color:#b8b8b8
}
.veui-searchbox.veui-disabled .veui-searchbox-icon,.veui-searchbox.veui-readonly .veui-searchbox-icon{cursor:not-allowed
}
.veui-searchbox.veui-disabled .veui-searchbox-icon-close,.veui-searchbox.veui-readonly .veui-searchbox-icon-close{display:none
}
.veui-searchbox.veui-disabled .veui-button,.veui-searchbox.veui-readonly .veui-button{background-clip:padding-box
}
.veui-searchbox-others{position:absolute;top:0;right:0;bottom:0;left:0;pointer-events:none;line-height:36px
}
.veui-searchbox-placeholder{padding:0 11px;color:#999
}
.veui-searchbox-placeholder-hide .veui-searchbox-placeholder{display:none
}
.veui-searchbox-icons{position:absolute;right:0;top:0;bottom:0
}
button.veui-searchbox-icon{outline:none;pointer-events:auto;color:#666;border:none;padding:0;width:auto;height:auto;background-color:transparent
}
button.veui-searchbox-icon .veui-icon{margin-right:1rem
}
button.veui-searchbox-icon .veui-button{vertical-align:unset
}
button.veui-searchbox-icon-close{display:none
}
button.veui-searchbox-icon-close .veui-icon{margin-right:8px
}
button.veui-searchbox-icon .veui-icon{vertical-align:middle
}
.veui-searchbox:not(.veui-disabled):not(.veui-readonly) .veui-searchbox-icon:hover{color:#333
}
.veui-searchbox.veui-searchbox-suggestion-expanded input,.veui-searchbox:not(.veui-disabled):not(.veui-readonly):hover input{border-color:#999
}
.veui-searchbox-suggestion-overlay{min-width:160px;max-height:280px;overflow-y:auto;background-color:#fff
}
.veui-searchbox-suggestion-overlay[ui~=large] .veui-searchbox-suggestion-item{font-size:16px
}
.veui-searchbox-suggestion-overlay[ui~=small] .veui-searchbox-suggestion-item{font-size:12px
}
.veui-searchbox[ui~=large]{height:42px;font-size:16px
}
.veui-searchbox[ui~=large] .veui-searchbox-others{line-height:42px
}
.veui-searchbox[ui~=large] .veui-searchbox-suggestion-item{height:42px;font-size:16px
}
.veui-searchbox[ui~=small]{height:32px;font-size:12px
}
.veui-searchbox[ui~=small] .veui-searchbox-others{line-height:32px
}
.veui-searchbox[ui~=small] .veui-searchbox-suggestion-item{height:32px;font-size:12px
}
.veui-searchbox[ui~=primary] .veui-searchbox-icon-search .veui-button{display:inline-block
}
.veui-searchbox[ui~=primary] .veui-searchbox-icon-search .veui-icon{display:none
}


